.navbar {
  background: rgb(27, 67, 50);
  height: 80px;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo {
  color: rgb(183, 228, 199);
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: flex;
  margin-right: 2rem;
  list-style: none;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: rgb(183, 228, 199);
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid rgb(64, 145, 108);
  color: rgb(64, 145, 108);
}

.fa-bars {
  color: rgb(183, 228, 199);
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

a {
  text-decoration: none;
  color: rgb(183, 228, 199);
}

a:hover {
  color: rgb(64, 145, 108);
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: rgb(27, 67, 50);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: rgb(183, 228, 199);
    color: rgb(27, 67, 50);
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: rgb(183, 228, 199);
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: rgb(183, 228, 199);
    padding: 14px 20px;
    border: 1px solid rgb(183, 228, 199);
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: rgb(183, 228, 199);
    color: rgb(27, 67, 50);
    transition: 250ms;
  }
}
